<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.tale-list {
  flex: 1;
}

.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

.search-form {
  text-align: right;
}

.input-with-select {
  max-width: 900px;
}
</style>

<template>
  <section class="wrap flex-column">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <div class="operation">
      <div style="width: 100%;text-align:left;margin-bottom: 20px;">
        <el-button type="primary" @click="addNewFrannchisee"></el-button>
      </div>
      <div class="search-form">
        <el-input placeholder="" v-model="selectInput" class="input-with-select" @keypress.native.enter="selectSearch">
          <el-select v-model="select" slot="prepend" placeholder="">
            <el-option label="" value="name"></el-option>
            <el-option label="APPID" value="app_id"></el-option>
          </el-select>
          <el-button slot="append" icon="el-icon-search" @click="selectSearch"></el-button>
        </el-input>
      </div>
    </div>
    <!-- / -->

    <div class="tale-list">
      <el-table :data="franchiseeList" border stripe style="min-width: 900px;">
        <el-table-column prop="id" label="ID" sortable></el-table-column>
        <el-table-column prop="user.username" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="app_id" label="APPID"></el-table-column>
        <el-table-column prop="secret" label="secret"></el-table-column>
        <el-table-column prop="template_id" label="idtemplate_id" show-overflow-tooltip></el-table-column>
        <el-table-column prop="created_at" label=""></el-table-column>
        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 1" class="success"></span>
            <span v-else class="warning"></span>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="" :width="280">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editBtn(scope.$index, scope.row)"></el-button>
            <el-button type="warning" size="small" @click="resetPassword(scope.row)"></el-button>
            <el-button v-if="scope.row.state === 1" type="danger" size="small" @click="stopFranchisee(scope.$index,scope.row)"></el-button>
            <el-button v-else type="info" size="small" @click="restartFranchisee(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="pages">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
      </el-pagination>
    </div>
    <!-- / -->

    <el-dialog title="" :visible.sync="editInfoDialog" center>
      <el-form label-position="right" label-width="80px" :model="editForm">
        <el-form-item label="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="APPID">
          <el-input v-model="editForm.app_id"></el-input>
        </el-form-item>
        <el-form-item label="secret">
          <el-input v-model="editForm.secret"></el-input>
        </el-form-item>
        <el-form-item label="id">
          <el-input v-model="editForm.template_id"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editInfoDialog = false"> </el-button>
        <el-button type="primary" @click="confirmEdit"> </el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      selectInput: "",
      select: "name",

      franchiseeList: [],
      count: 0,
      currentPage: 1,

      editInfoDialog: false,
      editForm: {
        name: "",
        app_id: "",
        secret: "",
        template_id: ""
      },

      currentEdit: {
        index: "",
        row: ""
      }
    };
  },

  created() {
    this.$api.getAppsList("", res => {
      this.franchiseeList = res.data.data;
      this.count = res.data.count;
    });
  },

  methods: {
    //
    editBtn(index, row) {
      this.currentEdit.index = index;
      this.currentEdit.row = row;
      this.editForm.app_id = row.app_id;
      this.editForm.secret = row.secret;
      this.editForm.template_id = row.template_id;
      this.editForm.name = row.name;
      this.editInfoDialog = true;
    },
    //
    confirmEdit() {
      const currentEdit = this.currentEdit;
      const editForm = this.editForm;
      this.$api.editAppUser(currentEdit.row.id, editForm, res => {
        this.$message({
          type: "success",
          message: ""
        });
        this.franchiseeList[currentEdit.index].name = editForm.name;
        this.franchiseeList[currentEdit.index].app_id = editForm.app_id;
        this.franchiseeList[currentEdit.index].secret = editForm.secret;
        this.franchiseeList[currentEdit.index].template_id =
          editForm.template_id;
        this.editInfoDialog = false;
      });
    },
    //
    addNewFrannchisee() {
      this.$router.push({ name: "franchiseeAdd" });
    },
    //
    selectSearch() {
      const getData = {
        [this.select]: this.selectInput
      };
      this.$api.getAppsList(getData, res => {
        this.franchiseeList = res.data.data;
        this.count = res.data.count;
      });
    },

    //
    resetPassword(row) {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          this.$api.postAppPassword(row.id, { password: value }, res => {
            this.$message({
              type: "success",
              message: " "
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    stopFranchisee(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteAppUser(row.id, res => {
            this.franchiseeList[index].state = 0;
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    restartFranchisee(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteAppUser(row.id, res => {
            this.franchiseeList[index].state = 1;
            this.$message({
              type: "success",
              message: ""
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },

    //
    handleCurrentChange(page) {
      const getData = {
        [this.select]: this.selectInput,
        page
      };
      this.$api.getAppsList(getData, res => {
        this.franchiseeList = res.data.data;
      });
    }
  }
};
</script>




