<style scoped>
.wrap {
  width: 100%;
  height: 100%;
}

.form-wrap {
  max-width: 900px;
  margin: 20px auto 0;
}
</style>

<template>
  <section class="wrap">
    <!--  -->
    <el-breadcrumb class="breadcrumb" separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/list/franchisee'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <div class="form-wrap">
      <el-form label-position="top" label-width="80px" :model="franchiseeForm" ref="franchiseeForm" :rules="rules">
        <el-form-item label="" prop="username">
          <el-input v-model="franchiseeForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="franchiseeForm.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="franchiseeForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="APPID" prop="app_id">
          <el-input v-model="franchiseeForm.app_id" placeholder="APPID"></el-input>
        </el-form-item>
        <el-form-item label="secret" prop="secret">
          <el-input v-model="franchiseeForm.secret" placeholder="secret"></el-input>
        </el-form-item>
        <el-form-item label=" id" prop="template_id">
          <el-input v-model="franchiseeForm.template_id" placeholder=" id"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('franchiseeForm')" style="width: 100%;"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      franchiseeForm: {
        username: "",
        name: "",
        password: "",
        app_id: "",
        secret: "",
        template_id: ""
      },

      rules: {
        username: [{ required: true, message: "" }],
        name: [{ required: true, message: "" }],
        password: [{ required: true, message: "" }],
        app_id: [{ required: true, message: "" }],
        secret: [{ required: true, message: "" }],
        template_id: [{ required: true, message: "" }]
      }
    };
  },

  methods: {
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.postAppUser(this.franchiseeForm, res => {
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/list/franchisee");
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>




